<template>
  <div class="container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>站点管理</el-breadcrumb-item>
      <el-breadcrumb-item>站点管理</el-breadcrumb-item>
      <el-breadcrumb-item>站点列表</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="tools">
      <label class="title" for="statusSelect">状态：</label>
      <el-select v-model="statusValue" id="statusSelect" class="selected" @change="statusChange">
        <el-option
          v-for="item in statusOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <label class="title" for="degreeSelect">紧急程度：</label>
      <el-select v-model="degreeValue" id="degreeSelect" class="selected" @change="degreeChange">
        <el-option
          v-for="item in degreeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary" class="rightButton" @click="addTask()">广告部投放列表</el-button>
    </div>
    <div class="tableContainer">
      <el-table
        :data="tableData"
        height="100%"
        border
        style="width: 100%" class="table">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="SPU">
                <span>{{ props.row.task_name }}</span>
              </el-form-item>
              <el-form-item label="库存">
                <span>{{ props.row.demand_people }}</span>
              </el-form-item>
              <el-form-item label="销量">
                <span>{{ props.row.translation_language }}</span>
              </el-form-item>
              <el-form-item label="标题">
                <span style="color: #00a0e9">{{ props.row.urgency_degree }}</span>
              </el-form-item>
              <el-form-item label="地区">
                <span>{{ props.row.translate_people }}</span>
              </el-form-item>
              <el-form-item label="开发人">
                <span>{{ props.row.designer }}</span>
              </el-form-item>
              <el-form-item label="组">
                <span style="color: #00a0e9">{{ props.row.status }}</span>
              </el-form-item>
              <el-form-item label="时间">
                <span style="color: #00a0e9">{{ props.row.status }}</span>
              </el-form-item>
              <el-form-item label="当日PV">
                <span style="color: #00a0e9">{{ props.row.status }}</span>
              </el-form-item>
              <el-form-item label="跳出率">
                <span style="color: #00a0e9">{{ props.row.status }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          prop="product_id"
          label="编号">
        </el-table-column>
        <el-table-column
          label="图片">
          <template slot-scope="scope">
            <img width="100" :src="scope.row.img" alt="">
          </template>
        </el-table-column>
        <el-table-column
          prop="category"
          label="分类">
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态">
        </el-table-column>
        <el-table-column
          prop="putIn"
          label="投放">
          <template slot-scope="scope">
            <el-button type="text" @click="dialogTableVisible = true">投放链接</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="cpa"
          label="CPA $">
        </el-table-column>
        <el-table-column
          prop="adv_dep_feedback"
          label="广告部反馈">
        </el-table-column>
        <el-table-column
          prop="spu"
          label="SPU">
        </el-table-column>
        <el-table-column
          prop="inventory"
          label="库存">
        </el-table-column>
        <el-table-column
          prop="sales"
          label="销量">
        </el-table-column>
        <el-table-column
          prop="title"
          label="标题">
        </el-table-column>
        <el-table-column
          prop="district"
          label="地区">
        </el-table-column>
        <el-table-column
          prop="selling_price"
          label="售价">
        </el-table-column>
        <el-table-column
          prop="developer"
          label="开发人">
        </el-table-column>
        <el-table-column
          prop="group"
          label="组">
        </el-table-column>
        <el-table-column
          prop="time"
          label="时间">
        </el-table-column>
        <el-table-column
          prop="today_pv"
          label="当日PV">
        </el-table-column>
        <el-table-column
          prop="bounce_rate"
          label="跳出率">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作">
          <template slot-scope="scope">
            <el-button @click="pushToRelease(scope.row)" type="text" size="small">推送到投放</el-button>
            <el-button @click="copy(scope.row)" type="text" size="small">复制</el-button>
            <el-button @click="updateWebsite(scope.row)" type="text" size="small">修改</el-button>
            <el-button @click="soldOut(scope.row)" type="text" size="small">下架</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[100, 200, 300, 400]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalSize">
      </el-pagination>
      <el-dialog title="生成投放链接" :visible.sync="dialogTableVisible">

      </el-dialog>
    </div>
  </div>
</template>

<script>
  const del = {
    '0': '否',
    '1': '是'
  }

  export default {
    name: "productList",
    data() {
      return {
        tableData: [],
        dialogTableVisible: false,
        totalSize: 0,
        currentPage: 1,
        pageSize: 100
      }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      pushToRelease(row) {//推送到投放

      },
      updateWebsite(row) {//修改

      },
      soldOut(row) {//下架

      },
      initTable() {
        let initList = [];
        let url = "/v1/product/releases?page=" + this.currentPage + "&pagesize=" + this.pageSize;
        let that = this;
        this.$axios.get(url).then((res) => {
          if (res.data.code == 200) {
            let productList = res.data.data.list;
            that.totalSize = parseInt(res.data.data.page_total);
            productList.forEach((product) => {
              let tempProduct = {};
              tempProduct.product_id = 1001;//编号
              tempProduct.img = 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=871521473,698358941&fm=27&gp=0.jpg';//图片
              tempProduct.category = '分类1';//分类
              tempProduct.status = '状态';//状态
              tempProduct.putIn = '投放';//投放
              tempProduct.cpa = 'cap';//cpa
              tempProduct.adv_dep_feedback = 'adv_dep_feedback';//广告部反馈
              tempProduct.spu = 'spu';//spu
              tempProduct.inventory = 'inventory';//库存
              tempProduct.sales = '销量';//销量
              tempProduct.title = 'title';//标题
              tempProduct.district = '地区';//地区
              tempProduct.selling_price = '售价';//售价
              tempProduct.developer = '开发人';//开发人
              tempProduct.group = '组';//组
              tempProduct.time = '时间';//时间
              tempProduct.today_pv = '当日PV';//当日PV
              tempProduct.bounce_rate = '跳出率';//跳出率
              initList.push(tempProduct);
            })
          }
          that.tableData = initList;
          console.log(res)
        }).catch((err) => {

        })
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .tools
      margin 20px 0
      .selected
        width: 150px
        margin-right: 20px
    .rightButton
      float: right
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 120px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



